.form__checkbox {
  position: relative;
  box-sizing: border-box;
  margin-top: 15px;
  padding-left: 26px;
  width: 260px;
  height: 30px;
  font-family: "PT Mono", monospace;
  font-size: 14px;
  line-height: 16px;
}
@media (max-width: 1219px) {
  .form__checkbox {
    margin-top: 10px;
  }
}
@media (max-width: 769px) {
  .form__checkbox {
    margin: 0 auto 25px;
  }
}

.form__checkbox,
.form__checkbox-mark,
.form__checkbox label {
  cursor: pointer;
}

.form__checkbox input {
  display: none;
}

.form__checkbox-mark {
  position: absolute;
  width: 15px;
  height: 15px;
  border-radius: 2px;
  border: 1px solid #ffffff;
  left: 0;
  top: 50%;
  bottom: 50%;
  margin: auto;
}

.form__checkbox input:checked ~ .form__checkbox-mark::after {
  content: "";
  position: absolute;
  top: 2px;
  left: 5px;
  width: 4px;
  height: 7px;
  border-bottom: 1px solid #ffffff;
  border-right: 1px solid #ffffff;
  transform: rotate(45deg);
}

